import React, { useState, useContext } from "react";
import UserContext from "../context/UserContext";
import { Link } from "react-router-dom";

export default function Loggingbar() {
  const { user } = useContext(UserContext);
  let role = " ";
  const [help, setHelp] = useState(false);

  switch (user && user.Role) {
    case "SCREW":
      role = " ";
      break;
    case "DIRECT":
      role = " ";
      break;
    case "AUTHCO":
      role = " ";
      break;
    case "PAKMATS":
      role = "";
      break;
    case "KAHAD":
      role = "  ";
      break;
    case "SCHOOL":
      role = " ";
      break;
    case "???":
      role = "???";
      break;
    case "ADMIN":
      role = "- ";
      break;
    default:
  }

  function toggleTextON() {
    setHelp(true);
  }

  function toggleTextOFF() {
    setHelp(false);
  }

  return (
    <div className="loggingbar">
      <h1 className="mainHeader">  {/*user?(user.MA):("")*/}</h1>
      {user === null ? (
        <>
          <p>  ,  </p>
          <Link to="/login"></Link>
        </>
      ) : (
        user && (
          <>
            {/*{user === null ? ( */}
            <p>
                <span style={{ fontSize: "10pt" }}>/</span>
              <span style={{ fontSize: "13pt" }}>  </span> 
              {user.NickName} (.. {user.MA}),   
              <button
                onClick={help ? toggleTextOFF : toggleTextON}
                className="justALinkNotButton"
              >
                <span className={help ? "" : "justALinkLink"}>{role}</span>
                <span> </span>
                <span className="justALinkLink">{help && "[ ]"}</span>
              </button>
            </p>
            {/*):(<br />)} */}
            {help && (
              <div className="Roles">
                <div className="RolesHeader">
                      :
                </div>
                <br />
                <div className="Roleheader">:</div>
                <br />
                <div className="Rolemeaning">  " </div>
                <br />
                <div className="Roleheader">  :</div>
                <br />
                <div className="Rolemeaning">
                    " +     
                </div>
                <br />
                <div className="Roleheader"> :</div>
                <br />
                <div className="Rolemeaning">
                      ,   "  
                   
                </div>
                <br />
                <div className="Roleheader"> :</div>
                <br />
                <div className="Rolemeaning"></div>
                <br />
                <div className="Roleheader"> :</div>
                <br />
                <div className="Rolemeaning"></div>
                <br />
                <div className="Roleheader"> :</div>
                <br />
                <div className="Rolemeaning"></div>
                <br />
                <div className="Roleheader">- :</div>
                <br />
                <div className="Rolemeaning"></div>
                <br />
                <br />
                <br />
                <button className="closeRoles" onClick={toggleTextOFF}>
                  
                </button>
                <br />
              </div>
            )}
          </>
        )
      )}
    </div>
  );
}
